
import { Vue } from 'vue-class-component';

export default class AddFilter extends Vue {
  temp1 = ""
  temp2 = ""
  temp3 = ""

    closeThis(){
        this.$emit("closeModalFilter");
    }

    createFilter(){
      var info: string = this.temp1 + " is between " + this.temp2 + " and " + this.temp3;
      this.$emit("createNewFilter", info);
      this.closeThis();
    }

}

